body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html{
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 20px;
}


html{
    background: black;
}


.enter {

    width: 80%;
    margin: 40px auto 40px auto;
    display: block;
    padding:20px;
    background: #8ab6fc;
    border: 2px solid #1a65dd;
    

}

.enter a {
    text-decoration: none;
    color:white;     
}

.enter a:hover {
    text-decoration: none;
    color:#9a1fd3;     
}

main {
    margin-top:50px;
}

.formularz{
    display: block;
}

.error{
    margin-left: 5px;
    color:red;
    font-size: 10px;
}

.titleform{
    margin-bottom:20px;
    display: block;
    text-align: center;
    font-size: 25px;
    font-weight: bold;
}

button{
    width: 100px;
    height: 40px;
    margin-top: 20px;
    background: green;
    color:white;
    border: 1px solid green;
    -webkit-transition: 0.2s;
    transition: 0.2s;
    cursor: pointer;
}

button:hover{
    color:green;
    background: white;
}
